<template>
  <div class="startCheck">
    <span class="time_class">
      {{ one }}
      :
      {{ two }}:
      {{ three }}
    </span>
    <span class="check_msg" v-if="checkFlag">
      <el-progress :text-inside="true" :stroke-width="18" :percentage="progressNum"></el-progress>
    </span>
    <span class="check_msg" v-if="!checkFlag">{{ checkMsg }}</span>
    <el-button @click="startCheck" style="margin-left:10px">{{ check }}</el-button>
    <check-finish :finishVisible.sync="finishFlag" :nape="nape" v-if="finishFlag"></check-finish>
  </div>
</template>

<script>
import checkFinish from "./checkFinish";
export default {
  components: { checkFinish },
  data() {
    return {
      flag: null,
      one: "00", // 时
      two: "00", // 分
      three: "00", // 秒
      abc: 0, // 秒的计数
      cde: 0, // 分的计数
      efg: 0, // 时的计数
      check: "开始检查",
      checkMsg: "请选择检查项目,开始检查......",
      progressNum: 0, //进度条
      startTimer: "",
      endTimer: "",
      checkFlag: false,
      finishFlag: false,
      nape: "" //项目
    };
  },
  // props: {
  //   progressStatus: {
  //     type: Boolean,
  //     default() {
  //       return false;
  //     }
  //   }
  // },
  // watch: {
  //   progressStatus(val) {
  //     if (val) {
  //       this.endProgress();
  //     }
  //   }
  // },
  methods: {
    // 开始计时
    startHandler() {
      this.flag = setInterval(() => {
        if (this.three === 60 || this.three === "60") {
          this.three = "00";
          this.abc = 0;
          if (this.two === 60 || this.two === "60") {
            this.two = "00";
            this.cde = 0;
            if (this.efg + 1 <= 9) {
              this.efg++;
              this.one = "0" + this.efg;
            } else {
              this.efg++;
              this.one = this.efg;
            }
          } else {
            if (this.cde + 1 <= 9) {
              this.cde++;
              this.two = "0" + this.cde;
            } else {
              this.cde++;
              this.two = this.cde;
            }
          }
        } else {
          if (this.abc + 1 <= 9) {
            this.abc++;
            this.three = "0" + this.abc;
          } else {
            this.abc++;
            this.three = this.abc;
          }
        }
      }, 1000 / 60);
    },
    // 暂停计时
    endHandler() {
      this.flag = clearInterval(this.flag);
    },
    // 开始检查
    startCheck(nape) {
      let flag = false;
      if (this.check == "重新检查") {
        // this.checkFlag = !this.checkFlag;
        this.progressNum = 0;
        this.one = "00";
        this.two = "00";
        this.three = "00";
        this.abc = 0;
        this.cde = 0;
        this.efg = 0;
        this.check = "暂停检查";
        flag = false;
        setTimeout(() => {
          this.startProgress(); //开始检查
          this.startHandler();
        }, 1000);
      }
      if (this.check == "开始检查") {
        this.checkFlag = !this.checkFlag;
        this.check = "暂停检查";
        flag = true;
      }
      if (this.check == "暂停检查" && flag) {
        this.startHandler(); //开始计时
        this.startProgress(); //开始检查
        // this.check = "继续检查";
      } else if (this.check == "暂停检查") {
        this.endHandler();
        this.endProgress();
        this.check = "继续检查";
      } else if (this.check == "继续检查") {
        this.startHandler(); //开始计时
        this.startProgress();
        this.check = "暂停检查";
      }
      if (nape != "") {
        console.log(nape);
        this.nape = nape;
      }
    },
    // 进度条
    startProgress() {
      this.startTimer = setInterval(() => {
        this.progressNum++;
        if (this.progressNum >= 100) {
          clearInterval(this.startTimer);
          this.endHandler();
          this.finishFlag = true;
          this.$emit("finishCheck", "(无违规项)");
          this.check = "重新检查";
        }
      }, 100);
    },
    endProgress() {
      clearInterval(this.startTimer);
      // this.endTimer = setInterval(() => {
      //   this.progressNum++;
      //   if (this.progressNum > 99) {
      //     clearInterval(this.endTimer);
      //     this.finishProgress();
      //   }
      // }, 10);
    },
    finishProgress() {
      this.$emit("finishProgress", false);
    }
  }
};
</script>

<style lang="less" scoped>
.startCheck {
  // margin-bottom: 8px;
  padding: 16px 8px 6px;
  display: flex;
  background: url(../../public/images/timebg.jpg) repeat;
  height: 32px;
  .time_class {
    display: inline-block;
    height: 30px;
    line-height: 30px;
    width: 120px;
    text-align: center;
    border: 1px solid #fff;
    background: #858c94;
    font-size: 20px;
    color: #fff;
    border-radius: 5px;
    margin-right: 25px;
  }
  .check_msg {
    font-weight: bold;
    font-size: 16px;
    display: inline-block;
    line-height: 40px;
    width: 85%;
    margin: auto;
    transform: scaleY(0.75);
  }
  .scaleProgress {
    transform: scaleY(0.75);
  }
  .el-button {
    height: 30px;
    line-height: 5px;
    font-size: 12px;
    // background: #c6e2ff;
  }
}
</style>
