<template>
  <el-dialog
    :modal="false"
    :visible="finishVisible"
    :modal-append-to-body="false"
    :append-to-body="true"
    top="45vh"
    @close="onClose()"
  >
    <div slot="title" class="title_class">
      <span>
        <img src="/images/title.png" width="15px" height="15px" alt />
      </span>
      <span>提示</span>
      <span>({{timelang}})秒后自动关闭</span>
    </div>

    <div class="tip_content">
      <div>
        <i class="el-icon-warning-outline"></i>
      </div>
      <div>
        <span>"{{nape}}"检查完成</span>
      </div>
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button type="primary" size="mini" @click="onClose">确 定</el-button>
    </span>
  </el-dialog>
</template>

<script >
export default {
  props: ["finishVisible", "nape"],
  data() {
    return {
      timelang: 3,
      timer: ""
    };
  },
  methods: {
    // 取消添加弹框
    onClose() {
      if (this.finishVisible) {
        this.$emit("update:finishVisible", false);
      }
    },
    settime() {
      this.timer = setInterval(() => {
        this.timelang--;
        if (this.timelang == 0) {
          this.onClose();
          this.cleartime();
        }
      }, 1000);
    },
    cleartime() {
      clearInterval(this.timer);
    }
  },
  mounted() {
    this.settime();
  }
};
</script>

<style lang="less" scoped>
/deep/.el-dialog {
  // background: #e5effa;
  width: 310px !important;
  height: 195px !important;
}
/deep/.el-dialog__header {
  height: 32px !important;
  padding: 0 !important;
  background: #5ca2ea !important; //   text-align: center;
  line-height: 32px !important;
  border-bottom: none !important;
}
/deep/.el-dialog__body {
  background: #fff !important;
  //   padding: none !important;
}
/deep/.el-dialog__footer {
  background: #fff !important;
  text-align: center;
  padding: 0 28px 6px;
}
/deep/.el-dialog__headerbtn {
  top: 5px;
  right: 10px;
  > i {
    color: #fff;
  }
}
.title_class {
  height: 30px;
  background: #61a1e9;
  line-height: 30px;
  > span:first-child {
    margin-left: 5px;
  }
  > span:nth-child(2) {
    color: #fff;
    margin: 5px;
  }
  > span:nth-child(3) {
    color: #fff;
  }
}
.tip_content {
  display: flex;
  height: 60px;
  > div:first-child {
    width: 50px;
    height: 50px;
    line-height: 40px;
    // margin: auto;
    i {
      font-size: 40px;
      text-align: center;
      color: #edb100;
    }
  }
  > div:nth-child(2) {
    span {
      line-height: 30px;
    }
  }
}
.dialog-footer {
  .el-button {
    // width: 100px;
    // height: 30px;
    // line-height: 30px;
  }
}
</style>