<template>
  <div class="check_content">
    <start-check></start-check>
    <el-row class="tac">
      <el-col :span="$store.state.leftCol" :class="leftClass" ref="leftRef">
        <el-menu
          default-active="2"
          class="el-menu-vertical-demo"
          @open="handleOpen"
          @close="handleClose"
        >
          <el-submenu index="1">
            <template slot="title">
              <i class="iconfont icon-wenjian icon_cla"></i>
              <span>常规文件检查</span>
            </template>
            <el-menu-item index="1-1" @click="toPath('checkTab','rule')">
              <div @contextmenu="showMenu('常规文件检查')">
                常规文件检查
                <!-- <vue-context-menu
                  :contextMenuData="contextMenuData"
                  @checkThis="checkThis"
                  @leading="leading"
                ></vue-context-menu>-->
              </div>
            </el-menu-item>
          </el-submenu>
          <el-submenu index="2">
            <template slot="title">
              <i class="iconfont icon-image icon_cla"></i>
              <span>图片文件检查</span>
            </template>
            <el-menu-item index="2-1" @click="toPath('checkTab','pic')">
              <div @contextmenu="showMenu('图片文件检查')">
                图片文件检查
                <!-- <vue-context-menu
                  :contextMenuData="contextMenuData"
                  @checkThis="checkThis"
                  @leading="leading"
                ></vue-context-menu>-->
              </div>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-col>
      <el-col :span="$store.state.rightCol">
        <router-view></router-view>
      </el-col>
    </el-row>
    <check-dialog :dialogVisible.sync="showDialog"></check-dialog>
    <vue-context-menu :contextMenuData="contextMenuData" @checkThis="checkThis" @leading="leading"></vue-context-menu>
    <pic-dialog :dialogVisible.sync="picDialogFlag"></pic-dialog>
  </div>
</template>
<script>
import startCheck from "@/components/startCheck";
import checkDialog from "@/views/fileCheck/checkTabDialog/checkDialog";
import picDialog from "@/views/fileCheck/checkTabDialog/picDialog";

export default {
  components: {
    startCheck,
    checkDialog,
    picDialog
  },
  data() {
    return {
      showDialog: false,
      picDialogFlag: false,
      leftClass: "small_style",
      // contextmenu data (菜单数据)
      contextMenuData: {
        // the contextmenu name(@1.4.1 updated)
        menuName: "demo",
        // The coordinates of the display(菜单显示的位置)
        axis: {
          x: null,
          y: null
        },
        // Menu options (菜单选项)
        menulists: [
          {
            fnHandler: "checkThis", // Binding events(绑定事件)
            icoName: "fa fa-home fa-fw", // icon (icon图标 )
            btnName: "检查此项(O)" // The name of the menu option (菜单名称)
          },
          {
            fnHandler: "leading",
            icoName: "fa fa-home fa-fw",
            btnName: "导出报告(R)"
          }
        ]
      }
    };
  },
  methods: {
    handleOpen() {
      // key, keyPath
      // console.log(key, keyPath);
    },
    handleClose() {
      // key, keyPath
      // console.log(key, keyPath);
    },
    toPath(path, type) {
      this.$router.push({
        name: path,
        params: {
          type: type
        }
      });
    },
    showMenu(type) {
      this.type = type;
      event.preventDefault();
      var x = event.clientX;
      var y = event.clientY;
      // Get the current location
      this.contextMenuData.axis = {
        x,
        y
      };
    },
    checkThis() {
      if (this.type == "图片文件检查") this.picDialogFlag = true;
      if (this.type == "常规文件检查") this.showDialog = true;
    },
    leading() {
      alert("导出文件");
    }
  }
};
</script>

